<template>
  <div class="container" style=''>
      <div class="btn" @click='$refs.photoBtn.click()'>
        <span v-if='oringinalPhoto==""'>請點選(以正面清晰照為主)</span>
        <span v-if='oringinalPhoto!=""'>已上傳</span>
        
      </div>
      <input id="inputimage" type="file" ref='photoBtn' class='hide' @change='onPhotoSelected'/>
      <img id="myImg" ref='imgDiv' class="hide" crossorigin="anonymous" :src='oringinalPhoto' style=''/>
      <canvas id="myCanvas" ref='canvasDiv' class='hide'></canvas>
  </div>  
</template>

<script>
import EXIF from "exif-js";
import "canvas-to-blob";

export default {
  name: "MoblieCameraBtn",

  data() {
    return {
      oringinalPhoto: "",
      photoOrientation: null,
      resetPhoto: null,
      maxWidth: 500,
      maxHeight: 500,
      callbackFrom: ""
    };
  },
  computed: {},
  mounted() {},
  created() {},
  methods: {
    onPhotoSelected(e) {
      let file = e.target.files[0];
      let reader = new FileReader();
      let vm = this;

      reader.onloadend = () => {
        console.log("load image end");

        vm.$refs.imgDiv.addEventListener("load", function() {
          vm.resetOrientation(vm.$refs.imgDiv.src);
        });

        this.getOrientation(file, function(originalOrientation) {
          console.log("photo originalOrientation: ", originalOrientation);
          vm.oringinalPhoto = reader.result;
          if (originalOrientation < 0) return;
          vm.photoOrientation = originalOrientation;
        });
      };
      reader.readAsDataURL(file);
    },
    getOrientation(file, callback) {
      var reader = new FileReader();
      reader.onload = function(e) {
        var view = new DataView(e.target.result);
        if (view.getUint16(0, false) != 0xffd8) return callback(-2);
        var length = view.byteLength,
          offset = 2;
        while (offset < length) {
          var marker = view.getUint16(offset, false);
          offset += 2;
          if (marker == 0xffe1) {
            if (view.getUint32((offset += 2), false) != 0x45786966)
              return callback(-1);
            var little = view.getUint16((offset += 6), false) == 0x4949;
            offset += view.getUint32(offset + 4, little);
            var tags = view.getUint16(offset, little);
            offset += 2;
            for (var i = 0; i < tags; i++)
              if (view.getUint16(offset + i * 12, little) == 0x0112)
                return callback(view.getUint16(offset + i * 12 + 8, little));
          } else if ((marker & 0xff00) != 0xff00) break;
          else offset += view.getUint16(offset, false);
        }
        return callback(-1);
      };
      reader.readAsArrayBuffer(file);
    },
    ratio(srcWidth, srcHeight, maxWidth, maxHeight) {
      var ratio = Math.min(maxWidth / srcWidth, maxHeight / srcHeight);
      return { width: srcWidth * ratio, height: srcHeight * ratio };
    },
    resetOrientation(srcBase64) {
      let srcOrientation = this.photoOrientation;
      let vm = this;
      // resetOrientation(e) {
      console.log("reset image");
      var img = new Image();

      img.onload = function() {
        var x = vm.ratio(img.width, img.height, vm.maxWidth, vm.maxHeight);
        img.width = x.width;
        img.height = x.height;

        var width = img.width,
          height = img.height,
          canvas = vm.$refs.canvasDiv,
          ctx = canvas.getContext("2d");

        if ([5, 6, 7, 8].indexOf(srcOrientation) > -1) {
          canvas.width = height;
          canvas.height = width;
        } else {
          canvas.width = width;
          canvas.height = height;
        }

        switch (srcOrientation) {
          case 2:
            ctx.transform(-1, 0, 0, 1, width, 0);
            break;
          case 3:
            ctx.transform(-1, 0, 0, -1, width, height);
            break;
          case 4:
            ctx.transform(1, 0, 0, -1, 0, height);
            break;
          case 5:
            ctx.transform(0, 1, 1, 0, 0, 0);
            break;
          case 6:
            ctx.transform(0, 1, -1, 0, height, 0);
            break;
          case 7:
            ctx.transform(0, -1, -1, 0, height, width);
            break;
          case 8:
            ctx.transform(0, -1, 1, 0, 0, width);
            break;
          default:
            ctx.transform(1, 0, 0, 1, 0, 0);
        }

        ctx.drawImage(img, 0, 0, img.width, img.height);

        // export base64
        vm.resetPhoto = canvas.toDataURL();

        if (vm.callbackFrom == "blob") {
          // canvas.toBlob(function(blob) {
          //   // Do something with the blob object,
          //   // e.g. creating a multipart form for file uploads:
          //   // var formData = new FormData();
          //   // formData.append("file", blob, fileName);
          //   vm.$emit("getResetPhoto", blob);
          //   /* ... */
          // }, "image/jpeg");
          var file = vm.dataURLtoFile(vm.resetPhoto, 'a.png');
          vm.$emit("getResetPhoto",file);

        } else {
          vm.$emit("getResetPhoto", canvas.toDataURL());
        }

        // callback && callback(canvas.toDataURL());
      };

      img.src = srcBase64;
    },
    dataURLtoFile(dataurl, filename) {
      var arr = dataurl.split(","),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], filename, { type: mime });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hide {
  display: none;
}

</style>



