<script>
import axios from "axios";
import firebase from "firebase";
import Game from "./Game";
import MoblieCameraBtn from "./MoblieCameraBtn";
import EXIF from "exif-js";

export default {
  name: "HelloWorld",

  data() {
    return {
      pageChannel: "video",
      loading: false,
      pageChannelArr: [
        "video",
        "form",
        "rule",
        "game",
        "board",
        "finish",
        "Ingredients",
        "ice"
      ],
      iceArr: [],
      userData: {
        hospital: "",
        username: "",
        photoName: "",
        score: 0,
        playTime: 0
      },
      serialNumber: "",
      userphotobase64: null,
      userBoardPhoto: "",
      videoId: "",
      videoArr: ["W9kGGmSfVeY", "RBcov7wMxlM"],
      videoVars: {
        autoplay: 1,
        rel: 0,
        showInfo: 0
      },
      scoreList: []
    };
  },
  watch: {
    iceArr: function(arr) {
      console.log(arr);
      if (arr.length > 3) {
        this.iceArr.shift();
      }
    }
  },
  computed: {
    sortScoreList: function() {
      let obj = this.scoreList.sort(function(a, b) {
        return b["score"] - a["score"] || a["playTime"] - b["playTime"];
      });
      return obj;
    },
    overTime: function() {
      var hour = new Date().getHours();
      return hour > 16;
    }
  },
  mounted() {
    console.log("created");
    // get url
    this.getUrlVideoId();
    // init fireBase
    this.initFirebase();
    // this.getBoardData();
    this.getSerialNumber();
  },
  created() {},
  methods: {
    playerEnd() {
      this.pageChannel = "form";
    },
    startGame() {
      if (this.overTime) {
        alert("現在已超過四點，成績將不列入排行");
      }
      this.pageChannel = "game";
    },
    gameover(data) {
      console.log(data.playTime, data.score);
      this.userData.score = data.score;
      this.userData.playTime = data.playTime;
      if (this.overTime) {
        this.getBoardData();
        this.pageChannel = this.pageChannelArr[4];
      } else {
        this.uploadUserData();
      }

      // this.getBoardData();
    },
    // onPhotoSelected(e) {
    //   let vm = this;
    //   this.userphoto = e.target.files[0];
    //   var reader = new FileReader();
    //   reader.onload = function() {
    //     vm.userBoardPhoto = reader.result;
    //   };
    //   reader.readAsDataURL(e.target.files[0]);
    // },
    uploadPhoto(e) {
      if (
        this.userphotobase64 == null ||
        this.userData.username == "" ||
        this.userData.hospital == ""
      ) {
        alert("請完成表單");
        return;
      }

      // var blob = this.dataURItoBlob(this.userphotobase64);
      // var formData = new FormData();
      // formData.append("canvasImage", blob);

      this.pageChannel = this.pageChannelArr[2];

      // const formData = new FormData();
      // formData.append("myFile", this.userphotobase64, 'hello');
      // axios
      //   .post(
      //     "https://us-central1-gsk-319c7.cloudfunctions.net/uploadFile",
      //     formData
      //   )
      //   .then(e => {
      //     console.log(e);
      //     this.userData.photoName = e.data.data;
      //   });

      var message = this.userphotobase64;
      this.storage
        .ref(this.userData.photoName)
        .putString(message, "data_url")
        .then(function(snapshot) {
          console.log("Uploaded a data_url string!");
        });
    },
    uploadUserData() {
      console.log("upload data");
      this.database.push(this.userData).then(e => {
        this.pageChannel = this.pageChannelArr[4];
        this.getBoardData();
      });
    },
    getUrlVideoId() {
      let vIndex = +this.getUrlParameter("vIndex") - 1;
      if (vIndex != NaN) {
        vIndex = vIndex < this.videoArr.length ? vIndex : 0;
        this.videoId = this.videoArr[vIndex];
      } else {
        this.videoId = this.videoArr[0];
      }
    },
    getUrlParameter(sParam) {
      var sPageURL = decodeURIComponent(window.location.search.substring(1)),
        sURLVariables = sPageURL.split("&"),
        sParameterName,
        i;

      for (i = 0; i < sURLVariables.length; i++) {
        sParameterName = sURLVariables[i].split("=");

        if (sParameterName[0] === sParam) {
          return sParameterName[1] === undefined ? true : sParameterName[1];
        }
      }
    },
    initFirebase() {
      var config = {
        apiKey: "AIzaSyAfSyBKDOds7c5qRLRHHg2Ug3RHLpNj_HE",
        authDomain: "gsk-319c7.firebaseapp.com",
        databaseURL: "https://gsk-319c7.firebaseio.com",
        projectId: "gsk-319c7",
        storageBucket: "gsk-319c7.appspot.com",
        messagingSenderId: "735059452550"
      };

      firebase.initializeApp(config);
      this.database = firebase.database().ref("posts/");
      this.serialDatabase = firebase.database().ref("serialNumber");
      this.storage = firebase.storage();
    },
    getBoardData() {
      this.loading = true;

      let vm = this;
      let storage = this.storage;
      this.database.limitToLast(10).once("value", function(data) {
        let scoreList = [];
        data.forEach(child => {
          scoreList.unshift(child.val());
        });
        prepareUrl(scoreList);
      });

      function prepareUrl(scoreList) {
        let count = 0;
        getUrl();
        function getUrl() {
          if (count >= scoreList.length) {
            console.log(scoreList);
            vm.scoreList = scoreList;
            vm.loading = false;
            return;
          }
          var imgRef = storage.ref(scoreList[count].photoName);
          imgRef.getDownloadURL().then(url => {
            scoreList[count].photoName = url;
            count++;
            getUrl();
          });
        }
      }
    },
    addIngredient(ingId) {
      let index = this.iceArr.indexOf(ingId);
      let arr = this.iceArr;
      if (index != -1) {
        arr.splice(index, 1);
      } else {
        if (arr.length >= 3) {
          alert("最多選三種配料，按已選擇配料可以取消");
          return;
        } else {
          arr.push(ingId);
        }
      }
    },
    getSerialNumber() {
      this.serialDatabase.once("value", data => {
        let num = +data.val();
        this.serialNumber = 1 + num;
        this.serialDatabase.set(this.serialNumber);
        this.serialNumber = this.addZero(4, this.serialNumber);
        this.userData.photoName = "photo-" + this.serialNumber;
      });
    },
    addZero(dig, num) {
      let count = dig;
      while (count > 0) {
        num = "0" + num;
        count--;
      }
      return num.slice(-dig);
    },
    getResetPhoto(base64) {
      console.log("getResetPhoto");
      this.userphotobase64 = base64;
    }
  },
  components: {
    Game,
    MoblieCameraBtn
  }
};
</script>

